.feedback {
  margin: 40px 0;
  display: flex;
  justify-content: space-between;

  &__inputs {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__text-fields {
    display: flex;
    gap: 20px;
  }
}

.text-fields__wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
